}
.ani_dy_x5 {
  animation-delay: .602s;
}
.ani_dy_x6 {
  animation-delay: .722s;
}
.upper {
  animation: upper .422s ease-in-out;
}
@keyframes upper {
0% {
    opacity: 0.382;
    transform: translateY(12px);
}
100% {
    opacity: 1;
    transform: translateY(0px);
}
}
.lefter {
  animation: lefter .422s ease-in-out;
}
@keyframes lefter {
0% {
    opacity: 0.382;
    transform: translateX(24px);
}
100% {
    opacity: 1;
    transform: translateX(0px);
}
}
.righter {
  animation: righter .422s ease-in-out;
}
@keyframes righter {
0% {
    opacity: 0.382;
    transform: translateX(-24px);
}
100% {
    opacity: 1;
    transform: translateX(0px);
}
}
.rot-180 {
  transform: rotate(180deg);
  transition: all .422s ease-in-out;
}
.upper_x2 {
  animation: upper_x2 .562s ease-in-out;
}
@keyframes upper_x2 {
0% {
    opacity: 0.382;
    transform: translateY(24px);
}
100% {
    opacity: 1;
    transform: translateY(0px);
}
}
.upper_mod, .modal .modal-center {
  animation: upper_mod .562s ease-in-out;
}
@keyframes upper_mod {
